let defaultPeople = '';
if(localStorage.getItem("People")) {
	defaultPeople = JSON.parse(localStorage.getItem("People"))
}
export default {
	People: defaultPeople,
	ManagerAsideList: [{
		name: "人员管理",
		icon: "icon-yonghu",
		clist: [{
			name: "学生管理",
			clist: [{
				name: "添加学生",
				router: "AdminAddStudent",
			}, {
				name: "查看学生",
				router: "AdminLookStudent"
			}]
		}, {
			name: "教师管理",
			clist: [{
				name: "添加教师",
				router: "AdminAddTeacher",
			}, {
				name: "查看教师",
				router: "AdminLookTeacher"
			}]
		}]
	}, {
		name: "班级管理",
		icon: "icon-kechengguanli",
		router: "AdminLookClass"
	}, {
		name: "听力题库管理",
		icon: "icon-jiantingfenxi",
		clist: [{
			name: "添加听力题目",
			router: "AdminAddAural",
		}, {
			name: "查看听力题目",
			router: "AdminLookAural"
		}]
	}, {
		name: "朗读题库管理",
		icon: "icon-ziyuan",
		router: "AdminLookRead"
	}],
	StudentAsideList: [{
		name: "查看学生",
		icon: "icon-yonghu",
		router: "TeacherLookStudent"
	}],
	TeacherAsideList: [{
		name: "查看学生",
		icon: "icon-yonghu",
		router: "TeacherLookStudent"
	}, {
		name: "添加口语题目",
		icon: "icon-ziyuan",
		router: "TeacherAddRead"
	}, {
		name: "布置听力作业",
		icon: "icon-ziyuan",
		router: "TeacherAssignListen"
	}, {
		name: "布置口语作业",
		icon: "icon-ziyuan",
		router: "TeacherAssignRead"
	}, {
		name: "修改密码",
		icon: "icon-ziyuan",
		router: "TeacherPassword"
	}, {
		name: "个人信息",
		icon: "icon-ziyuan",
		router: "TeacherInfo"
	}, {
		name: "导出学生口语记录",
		icon: "icon-ziyuan",
		router: "OutHistory"
	}]
}


